.wrap {
  min-width: 375px;
  display: flex;
  flex-direction: column;
  align-items: center;

  .cardBlock {
    display: flex;
    flex-direction: column;

    .card {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 40px 0;

      .card_description {
        font-size: 16pt;
        opacity: .6;
      }

      .card_data {
        font-size: 20pt;
        font-weight: bold;
      }
    }
  }

  .privacy {
    display: flex;

    .privacy_card {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 0 20px;
    }
  }
}












